import React, { useEffect,useState } from 'react';
import UploadComponent from './components/ui/upload';
import * as Themes from './config/Themes';
import UploadDialogComponent from './components/ui/upload';

import { makeConfigProvide } from './provide/ConfigProvide'
import * as types from './types/app'
import * as commonTypes from './types/common'

// 
export type UploadDialogProps = {
  themeName?:string,// 
  locale?: string,// en|zhzh
  headers?: any,// 
  apiUrls?: types.ApiUrls,// 
  attachPrefix?: string,// 
  // 
  open: boolean, // true|false
  success: Function, // attach|attaches|url
  onClose: Function,// 
  type: string, // image|images|file|files,video|videos|audio|audios|flash|flashes
  attaches?: commonTypes.Attach[], // 
  url?: string, // url
  fromEditor?: boolean//  
}
// 
export type UploaderProps = {
  themeName?:string,// 
  locale?: string,// en|zhzh
  headers?: any,// 
  apiUrls?: types.ApiUrls,// 
  attachPrefix?: string,// 
  // 
  type: string,// image|images|file|files,video|videos|audio|audios|flash|flashes
  onChange: Function, // {attach|attaches|url}
  cancel?: Function, // 
  defaultUrl?: string,// url
  defaultUrls?: string[], // urls
  defaultAttach?: commonTypes.Attach,// 
  defaultAttaches?: commonTypes.Attach[]// 
}

export const Upload = (props: UploaderProps) => {
  // 
  const {
    headers,
    apiUrls,
    locale,
    attachPrefix,
    themeName,
    ...rest
  } = props
  const [theme,setTheme] = useState(Themes.defaultTheme)
  const { initConfig } = makeConfigProvide()
  useEffect(()=>{
    initConfig({
      headers,
      apiUrls,
      locale,
      attachPrefix,
      themeName,
      loaded:(config:types.Config)=>{
        setTheme(config.theme)
      }
    })
  },[])
  return <div></div>
  // return <UploadComponent {...rest} />
}
export const UploadDialog = (props: UploadDialogProps) => {
  // 
  const {
    headers,
    apiUrls,
    locale,
    attachPrefix,
    themeName,
    ...rest
  } = props
  const [theme,setTheme] = useState(Themes.defaultTheme)
  const { initConfig } = makeConfigProvide()
  initConfig({
    headers,
    apiUrls,
    locale,
    attachPrefix,
    themeName,
    loaded:(config:types.Config)=>{
      setTheme(config.theme)
    }
  })
  return <UploadDialogComponent  {...rest} />
}
export default Upload